import { supabase } from '../config/supabase'

export class CycleService {
  //   
  static async getAllCycles() {
    try {
      // Supabase      
      if (!supabase) {
        console.warn('Supabase     .')
        const localCycles = localStorage.getItem('savedCycles')
        return localCycles ? JSON.parse(localCycles) : []
      }

      const { data, error } = await supabase
        .from('cycles')
        .select('*')
        .order('created_at', { ascending: false })
      
      if (error) throw error
      return data || []
    } catch (error) {
      console.error('  :', error)
      //       
      try {
        const localCycles = localStorage.getItem('savedCycles')
        return localCycles ? JSON.parse(localCycles) : []
      } catch (localError) {
        console.error('   :', localError)
        return []
      }
    }
  }

  //  
  static async saveCycle(cycleData) {
    try {
      // Supabase      
      if (!supabase) {
        console.warn('Supabase     .')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const newCycle = {
          id: Date.now(),
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          created_at: new Date().toISOString()
        }
        localCycles.unshift(newCycle)
        localStorage.setItem('savedCycles', JSON.stringify(localCycles))
        return newCycle
      }

      const { data, error } = await supabase
        .from('cycles')
        .insert([{
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          created_at: new Date().toISOString()
        }])
        .select()
      
      if (error) throw error
      return data[0]
    } catch (error) {
      console.error('  :', error)
      //      
      try {
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const newCycle = {
          id: Date.now(),
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          created_at: new Date().toISOString()
        }
        localCycles.unshift(newCycle)
        localStorage.setItem('savedCycles', JSON.stringify(localCycles))
        return newCycle
      } catch (localError) {
        console.error('   :', localError)
        throw new Error('  .')
      }
    }
  }

  //  
  static async updateCycle(id, cycleData) {
    try {
      // Supabase      
      if (!supabase) {
        console.warn('Supabase     .')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const index = localCycles.findIndex(cycle => cycle.id === id)
        if (index > -1) {
          localCycles[index] = {
            ...localCycles[index],
            name: cycleData.name,
            content: cycleData.content,
            selected_items: cycleData.selectedItems,
            updated_at: new Date().toISOString()
          }
          localStorage.setItem('savedCycles', JSON.stringify(localCycles))
          return localCycles[index]
        }
        throw new Error('   .')
      }

      const { data, error } = await supabase
        .from('cycles')
        .update({
          name: cycleData.name,
          content: cycleData.content,
          selected_items: cycleData.selectedItems,
          updated_at: new Date().toISOString()
        })
        .eq('id', id)
        .select()
      
      if (error) throw error
      return data[0]
    } catch (error) {
      console.error('  :', error)
      throw new Error('  .')
    }
  }

  //  
  static async deleteCycle(id) {
    try {
      // Supabase      
      if (!supabase) {
        console.warn('Supabase     .')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        const filteredCycles = localCycles.filter(cycle => cycle.id !== id)
        localStorage.setItem('savedCycles', JSON.stringify(filteredCycles))
        return true
      }

      const { error } = await supabase
        .from('cycles')
        .delete()
        .eq('id', id)
      
      if (error) throw error
      return true
    } catch (error) {
      console.error('  :', error)
      throw new Error('  .')
    }
  }

  //  ID 
  static async getCycleById(id) {
    try {
      // Supabase      
      if (!supabase) {
        console.warn('Supabase     .')
        const localCycles = JSON.parse(localStorage.getItem('savedCycles') || '[]')
        return localCycles.find(cycle => cycle.id === id)
      }

      const { data, error } = await supabase
        .from('cycles')
        .select('*')
        .eq('id', id)
        .single()
      
      if (error) throw error
      return data
    } catch (error) {
      console.error('  :', error)
      throw new Error('   .')
    }
  }
}
