import { ref } from 'vue'

export function useAppState() {
  //  
  const currentPage = ref(1)
  const sidebarOpen = ref(false)
  
  //   
  const selectedItems = ref([])
  const savedCycles = ref([])
  

  

  
  //  
  const menuItems = [
    { text: ' ', icon: 'dashboard-icon' },
    { text: ' ', icon: 'messages-icon' },
    { text: '  ', icon: 'schedule-icon' }
  ]
  
  // localStorage  
  const loadAppState = (onLoadComplete) => {
    try {
      console.log('loadAppState ')
      const savedSelectedItems = localStorage.getItem('selectedItems')
      console.log('localStorage  selectedItems:', savedSelectedItems)
      if (savedSelectedItems) {
        selectedItems.value = JSON.parse(savedSelectedItems)
        console.log('selectedItems :', selectedItems.value)
      } else {
        console.log(' selectedItems ')
      }
      
      //     
      if (onLoadComplete && typeof onLoadComplete === 'function') {
        console.log('  ')
        onLoadComplete()
      } else {
        console.log('  ')
      }
    } catch (error) {
      console.error('    :', error)
    }
  }
  
  // localStorage  
  const saveAppState = () => {
    try {
      localStorage.setItem('selectedItems', JSON.stringify(selectedItems.value))
    } catch (error) {
      console.error('    :', error)
    }
  }
  
  //    ( )
  loadAppState()
  
  return {
    // 
    currentPage,
    sidebarOpen,
    selectedItems,
    savedCycles,
    menuItems,
    
    // 
    loadAppState,
    saveAppState
  }
}
