import { ref } from 'vue'

export function useTextArea() {
  const textAreaContent = ref('')
  const rightPanelTitle = ref('NC Code')
  const textAreaPlaceholder = ref(' NC  ...')
  
  // localStorage textAreaContent 
  const loadTextAreaContent = () => {
    try {
      const savedContent = localStorage.getItem('textAreaContent')
      if (savedContent) {
        textAreaContent.value = savedContent
      }
    } catch (error) {
      console.error('     :', error)
    }
  }
  
  //  
  loadTextAreaContent()
  
  const updateContent = (content) => {
    console.log('useTextArea updateContent , content:', content)
    textAreaContent.value = content
    // localStorage  
    try {
      localStorage.setItem('textAreaContent', content)
      console.log('localStorage textAreaContent ')
    } catch (error) {
      console.error('     :', error)
    }
  }
  
  const updateTitle = (title) => {
    rightPanelTitle.value = title
  }
  
  const clearContent = () => {
    console.log('useTextArea clearContent ')
    textAreaContent.value = ''
    // localStorage 
    try {
      localStorage.removeItem('textAreaContent')
      console.log('localStorage textAreaContent ')
    } catch (error) {
      console.error('     :', error)
    }
  }
  
  const appendContent = (content) => {
    if (textAreaContent.value && textAreaContent.value.trim() !== '') {
      textAreaContent.value += '\n\n' + content
    } else {
      textAreaContent.value = content
    }
  }
  
  const updateTextAreaForPage = (pageNumber) => {
    switch (pageNumber) {
      case 1:
        textAreaPlaceholder.value = ' NC  ...'
        //       (  )
        break
      case 2:
        textAreaPlaceholder.value = '   NC  ...'
        textAreaContent.value = ''
        break
      case 3:
        textAreaPlaceholder.value = '   ...'
        textAreaContent.value = ''
        break
      case 4:
        textAreaPlaceholder.value = '3D   ...'
        textAreaContent.value = ''
        break
      default:
        textAreaPlaceholder.value = 'NC Code'
        textAreaContent.value = ''
    }
  }
  
  const showButtonCode = (buttonName, buttonCodes) => {
    if (buttonCodes[buttonName]) {
      //      
      textAreaContent.value = buttonCodes[buttonName]
    } else {
      textAreaContent.value = `// ${buttonName}    .`
    }
  }
  
  return {
    textAreaContent,
    rightPanelTitle,
    textAreaPlaceholder,
    updateContent,
    updateTitle,
    clearContent,
    appendContent,
    updateTextAreaForPage,
    showButtonCode
  }
}
